import {
	api,
	postajax
} from '@/utils/api.js'
export default {
	data() {
		return {
			focus: 0,
			username: '',
			password: ''
			
		};
	},
	methods: {
		focustxt(type) {
			this.focus = type;
		},
		blurtxt() {
			this.focus = 0;
		},
		login() {
			if (this.password.trim() && this.username.trim()) {
				postajax(api.login, {
					username: this.username.trim(),
					password: this.password.trim()
				}).then((da)=>{
					console.log(da)
					if(da.code==0){
						uni.switchTab({
							url:'/pages/index/index'
						})
						
					}
				})
			} else {
				uni.showToast({
					title: '请输入正确的用户名和密码',
					icon: 'none'
				})
			}
		}
	}
};
